                "": '#7766EE',
                '': '#BB5544',
                '': '#704170',
                '': '#afa981'
            },
        }
    },
    created(){
        this.characteristicLoad()
    },
    computed: {
        gradientStyle() {
            const defaultColor = '#FFFFFF'; // 
            const startColor = this.colorMap[this.pokemon.type1] || defaultColor;
            const endColor = this.colorMap[this.pokemon.type2] || defaultColor;
            return `background: linear-gradient(to right, ${startColor}, ${endColor})`;
        },
    },
    methods :{
        characteristicLoad(){
            this.filteredData = this.tableData.filter(item => {
                return item.ability1.toLowerCase().includes(this.elf.toLowerCase())||
                    item.ability2.toLowerCase().includes(this.elf.toLowerCase()) ||
                    item.abilityHide.toLowerCase().includes(this.elf.toLowerCase());
            })
            .sort((a,b) => a.id - b.id);
        },
        getImage(id) {
            // 
            return require('@/assets/images/normal/' + id + '.png')
        },
        details(row) {
            localStorage.setItem("information", JSON.stringify(row))
            this.$router.push("/wiki")
        },
    }
}
